import React, {
  ComponentPropsWithRef,
  useCallback,
  useEffect,
  useState,
} from "react";
import { EmblaCarouselType } from "embla-carousel";
import { IServicePreview } from "../../types/services.type";

type UseDotButtonType = {
  selectedIndex: number;
  onServiceButtonClick: (index: number) => void;
};

export const useServicesButtons = (
  emblaApi: EmblaCarouselType | undefined,
  onButtonClick?: (emblaApi: EmblaCarouselType) => void
): UseDotButtonType => {
  const [selectedIndex, setSelectedIndex] = useState(0);

  const onServiceButtonClick = useCallback(
    (index: number) => {
      if (!emblaApi) return;
      emblaApi.scrollTo(index);
      if (onButtonClick) onButtonClick(emblaApi);
    },
    [emblaApi, onButtonClick]
  );

  const onSelect = useCallback((emblaApi: EmblaCarouselType) => {
    setSelectedIndex(emblaApi.selectedScrollSnap());
  }, []);

  useEffect(() => {
    if (!emblaApi) return;

    onSelect(emblaApi);

    emblaApi.on("reInit", onSelect).on("select", onSelect);
  }, [emblaApi, onSelect]);

  return {
    selectedIndex,
    onServiceButtonClick,
  };
};

interface IServiceCarouselButtonProps extends ComponentPropsWithRef<"button"> {
  service: IServicePreview;
}

export const ServiceCarouselButton = ({
  service,
  className,
  ...attr
}: IServiceCarouselButtonProps) => {
  const {
    frontmatter: {
      title,
      image: { publicURL: imageURL },
    },
  } = service;

  return (
    <button
      className={`flex gap-5 h-10 items-center ${className}`}
      type="button"
      {...attr}
    >
      <img src={imageURL} alt={`${title} logo`} className="invert h-7 w-7" />
      <h2
        className="hidden lg:block font-semibold text-lg text-secondary-100 text-start"
        itemProp="serviceType"
      >
        {title}
      </h2>
    </button>
  );
};
