import React, { useCallback, useEffect, useRef } from "react";
import { EmblaCarouselType, EmblaEventType } from "embla-carousel";
import useEmblaCarousel from "embla-carousel-react";

import { IServicePreview } from "../../types/services.type";

import ServicePreview from "./servicePreview";
import {
  ServiceCarouselButton,
  useServicesButtons,
} from "./servicesCarouselButtons";

const TWEEN_FACTOR_BASE = 0.2;
const numberWithinRange = (number: number, min: number, max: number): number =>
  Math.min(Math.max(number, min), max);

const ServicesCarousel = ({ services }: { services: IServicePreview[] }) => {
  const [emblaRef, emblaApi] = useEmblaCarousel({
    axis: "y",
    loop: true,
    align: "center",
  });

  const tweenFactor = useRef(0);
  const tweenNodes = useRef<HTMLElement[]>([]);

  const { selectedIndex, onServiceButtonClick } = useServicesButtons(emblaApi);

  const setTweenNodes = useCallback((emblaApi: EmblaCarouselType): void => {
    tweenNodes.current = emblaApi.slideNodes().map((slideNode) => {
      return slideNode.querySelector(".service-preview") as HTMLElement;
    });
  }, []);

  const setTweenFactor = useCallback((emblaApi: EmblaCarouselType) => {
    tweenFactor.current = TWEEN_FACTOR_BASE * emblaApi.scrollSnapList().length;
  }, []);

  const tweenScale = useCallback(
    (emblaApi: EmblaCarouselType, eventName?: EmblaEventType) => {
      const engine = emblaApi.internalEngine();
      const scrollProgress = emblaApi.scrollProgress();
      const slidesInView = emblaApi.slidesInView();
      const isScrollEvent = eventName === "scroll";

      emblaApi.scrollSnapList().forEach((scrollSnap, snapIndex) => {
        let diffToTarget = scrollSnap - scrollProgress;
        const slidesInSnap = engine.slideRegistry[snapIndex];

        slidesInSnap.forEach((slideIndex) => {
          if (isScrollEvent && !slidesInView.includes(slideIndex)) return;

          if (engine.options.loop) {
            engine.slideLooper.loopPoints.forEach((loopItem) => {
              const target = loopItem.target();

              if (slideIndex === loopItem.index && target !== 0) {
                const sign = Math.sign(target);

                if (sign === -1) {
                  diffToTarget = scrollSnap - (1 + scrollProgress);
                }
                if (sign === 1) {
                  diffToTarget = scrollSnap + (1 - scrollProgress);
                }
              }
            });
          }

          const tweenValue = 1 - Math.abs(diffToTarget * tweenFactor.current);
          const scale = numberWithinRange(tweenValue, 0, 1);
          const tweenNode = tweenNodes.current[slideIndex];
          tweenNode.style.transform = `scale(${scale})`;
        });
      });
    },
    []
  );

  useEffect(() => {
    if (!emblaApi) return;

    setTweenNodes(emblaApi);
    setTweenFactor(emblaApi);
    tweenScale(emblaApi);

    emblaApi
      .on("reInit", setTweenNodes)
      .on("reInit", setTweenFactor)
      .on("reInit", tweenScale)
      .on("scroll", tweenScale)
      .on("slideFocus", tweenScale);
  }, [emblaApi, tweenScale]);

  return (
    <>
      <div className="hidden sm:flex flex-col gap-5 p-8 lg:basis-[410px] shrink-0">
        {services
          ? services.map((service, i) => (
              <ServiceCarouselButton
                key={`dot-${service.id}`}
                service={service}
                onClick={() => onServiceButtonClick(i)}
                className={`${i === selectedIndex ? "" : "brightness-[0.4]"}`}
              />
            ))
          : null}
      </div>
      <div ref={emblaRef} className="overflow-hidden basis-full">
        <div
          style={{ backfaceVisibility: "hidden" }}
          className="flex flex-col touch-pan-x h-[1000px] sm:h-[900px] md:h-[750px]"
        >
          {services
            ? services.map((service, i) => (
                <div
                  key={service.id}
                  className="grow-0 shrink-0 basis-1/3 min-h-0 select-none flex items-center"
                >
                  <ServicePreview
                    service={service}
                    className={`select-none ${
                      i === selectedIndex ? "" : "brightness-[0.4]"
                    }`}
                  />
                </div>
              ))
            : null}
        </div>
      </div>
    </>
  );
};

export default ServicesCarousel;
