import React from "react";

import { IServicePreview } from "../../types/services.type";

const ServicePreview = ({
  service,
  className,
}: {
  service: IServicePreview;
  className?: string;
}) => {
  const {
    html,
    frontmatter: {
      title,
      keywords,
      image: { publicURL: imageURL },
    },
  } = service;

  return (
    <div
      className={`service-preview origin-[5%_50%] break-inside-avoid flex flex-col gap-5 p-8 text-secondary-100 cursor-pointer ${className}`}
      itemScope
      itemType="https://schema.org/Service"
    >
      <div className="flex items-center gap-5">
        <img
          src={imageURL}
          alt={`${title} logo`}
          className="invert h-10 w-10"
        />
        <h2 className="font-bold text-2xl" itemProp="serviceType">
          {title}
        </h2>
      </div>
      <div
        className={`text-base text-secondary-70`}
        dangerouslySetInnerHTML={{ __html: html }}
        itemProp="description"
      />
      {keywords ? (
        <div className="flex gap-2 flex-wrap w-full">
          {keywords.map((hashtag) => (
            <p className="h-8 flex items-center whitespace-nowrap px-4 bg-main-70 text-secondary-70 rounded-lg">
              {hashtag}
            </p>
          ))}
        </div>
      ) : null}
    </div>
  );
};

export default ServicePreview;
